.Modal {
    background-color: #fff;
    border: 1px black solid;
    display: flex;
    flex-direction: column;
    padding: 25px 50px;
    align-items: center;
    h2 {
        font-size: 30px;
        font-weight: 900;
        color: #24322F;
        text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.25);
        text-align: center;
        margin-bottom: 40px;
    }
    input {
        width: 350px;
        height: 50px;
        padding: 15px 5px;
        background: #D6EAEC;
        margin-bottom: 15px;
    }
    button {
        background: #24322F;
        font-size: 20px;
        font-weight: 900;
        color: #D6EAEC;
        text-shadow: 5px 5px 5px rgba(0, 0, 0, 0.25);
        width: 250px;
        height: 50px;
    }
}